import { authenticator } from "~/services/auth"

export let loader = async ({ request }) => {
  await authenticator.isAuthenticated(request, {
    successRedirect: "/",
  })

  return {
    articles,
  }
}

export default function Index() {
  return (
    <div
      style={{
        flexDirection: "row",
        display: "flex",
        height: "100%",
      }}
    >
      as
    </div>
  )
}
